import { memoryParse } from './memoryParse.js';

function isPureVariable(path) {
    if (typeof path === 'string') {
        try {
            var ast = memoryParse(path);
            // 
            return ast.body.length === 1 && ast.body[0].type === 'script';
        }
        catch (err) {
            return false;
        }
    }
    return false;
}

export { isPureVariable };
