import 'tslib';
import 'amis-formula';
import 'moment';
import 'lodash/isPlainObject';
import { resolveVariableAndFilter } from './resolveVariableAndFilter.js';
import './filter.js';
import mapValues from 'lodash/mapValues';
import camelCase from 'lodash/camelCase';
import { valueMap } from './style-helper.js';

/**
 *  helper 
 */
function autoAddImageURL(image) {
    //  linear-gradient 
    if (typeof image === 'string' &&
        image.indexOf(',') === -1 &&
        image.indexOf('(') === -1) {
        return "url(\"".concat(image, "\")");
    }
    return image;
}
/**
 *  style
 * 1. 
 * 2.  font-size  fontSize
 * 3.  image  url
 */
function buildStyle(style, data) {
    if (!style) {
        return style;
    }
    var styleVar = typeof style === 'string'
        ? resolveVariableAndFilter(style, data, '| raw') || {}
        : mapValues(style, function (s) { return resolveVariableAndFilter(s, data, '| raw') || s; });
    Object.keys(styleVar).forEach(function (key) {
        if (key === 'radius') {
            styleVar['borderRadius'] =
                styleVar.radius['top-left-border-radius'] +
                    ' ' +
                    styleVar.radius['top-right-border-radius'] +
                    ' ' +
                    styleVar.radius['bottom-right-border-radius'] +
                    ' ' +
                    styleVar.radius['bottom-left-border-radius'];
            delete styleVar['radius'];
        }
        // background-color => backgroundColorcss variable--colors-brand-5
        if (key.indexOf('-') > 0) {
            styleVar[camelCase(valueMap[key] || key)] = styleVar[key];
            delete styleVar[key];
        }
    });
    if (styleVar.backgroundImage) {
        styleVar.backgroundImage = autoAddImageURL(styleVar.backgroundImage);
    }
    if (styleVar.borderImage) {
        styleVar.borderImage = autoAddImageURL(styleVar.borderImage);
    }
    if (styleVar.listStyleImage) {
        styleVar.listStyleImage = autoAddImageURL(styleVar.listStyleImage);
    }
    return styleVar;
}

export { buildStyle };
